function readFilePromise(file) {
  const reader = new FileReader();
  return new Promise((resolve, reject) => {
    reader.readAsText(file, 'UTF-8');

    reader.onload = function (evt) {
      resolve(evt.target.result);
    }
    reader.onerror = function (evt) {
      reject('Error reading CSS file');
    }
  });
}

new Vue({
  el: '#app',
  data: {
    fileSelected: false,
    isDragging: false,
    inputHasFocus: false,
    totalFiles: 0,
  },
  mounted() {

  },
  methods: {
    readFiles(droppedFiles) {
      return Promise.all(Array.from(droppedFiles).map(file => readFilePromise(file)))
        .then(data => data.join(''));
    },
    drop(e) {
      this.isDragging = false;
      this.fileSelected = true;
      droppedFiles = e.dataTransfer.files;
      this.totalFiles = droppedFiles.length;
      this.readFiles(droppedFiles).then(data => console.log(data.length));
      console.log(droppedFiles);
    },
    fileInputChange(e) {
      this.isDragging = false;
      this.fileSelected = true;
      droppedFiles = e.target.files;
      this.totalFiles = droppedFiles.length;
      this.readFiles(droppedFiles);
      console.log(droppedFiles);
    }
  },
});
